.tree-list {
  @include reset-list;
  margin-bottom: 1rem;

  > li {
    padding-left: 1em;
  }

  &.no-pad > li {
    padding-left: 0;
  }
}

.tree-item {
  padding-left: 1.25em;

  &.has-items {
    padding-left: 0;
    cursor: pointer;
  }
}



